import "./App.css";
import { db } from "./firebase";
import { ref, onValue } from "firebase/database";
import { useState, useEffect } from "react";
import { createTheme, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Header from './Header';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import { Line } from "react-chartjs-2";
import Chart from 'chart.js/auto';

const theme = createTheme();


function App() {
  const [tempsS1, setTempsS1] = useState([]);
  const [timesS1, setTimesS1] = useState([]);
  const [tempsS2, setTempsS2] = useState([]);
  const [timesS2, setTimesS2] = useState([]);
  const [tempsS3, setTempsS3] = useState([]);
  const [timesS3, setTimesS3] = useState([]);
  const [tempsS4, setTempsS4] = useState([]);
  const [timesS4, setTimesS4] = useState([]);

  const [isShown, setIsShown] = useState(false);
  const [isShown2, setIsShown2] = useState(false);
  const [isShown3, setIsShown3] = useState(false);
  const [isShown4, setIsShown4] = useState(false);

  const handleClick1 = event => {
    //  toggle shown state
    setIsShown(current => !current);
    setIsShown2(false);
    setIsShown3(false);
    setIsShown4(false);
  };

  const handleClick2 = event => {
    //  toggle shown state
    setIsShown2(current => !current);
    setIsShown(false);
    setIsShown3(false);
    setIsShown4(false);
  };

  const handleClick3 = event => {
    //  toggle shown state
    setIsShown3(current => !current);
    setIsShown2(false);
    setIsShown(false);
    setIsShown4(false);
  };

  const handleClick4 = event => {
    //  toggle shown state
    setIsShown4(current => !current);
    setIsShown2(false);
    setIsShown3(false);
    setIsShown(false);
  };

  function notEmptyTemp(value) {
    return value !== "";
  }

  //read
  useEffect(() => {
    onValue(ref(db), (snapshot) => {
      setTempsS1([]);
      const data = snapshot.val();
      if (data !== null) {
        var dictionary = Object.values(data).at(0).SensorData1; 
        var keys = Object.keys(dictionary);
        
        keys.map((time) => {
          setTimesS1((oldArray) => 
          [...oldArray, dictionary[time].substring(0,dictionary[time].indexOf("Temp:"))]);
        });
        
        
        keys.map((temp) => {
          setTempsS1((oldArray) => 
          [...oldArray, parseFloat(dictionary[temp].substring(dictionary[temp].indexOf("Temp:")+5))]);
        });

        var dictionary2 = Object.values(data).at(0).SensorData2; 
        var keys2 = Object.keys(dictionary2);
        
        keys2.map((time) => {
          setTimesS2((oldArray) => 
          [...oldArray, dictionary2[time].substring(0,dictionary2[time].indexOf("Temp:"))]);
        });
        
        
        keys2.map((temp) => {
          setTempsS2((oldArray) => 
          [...oldArray, parseFloat(dictionary2[temp].substring(dictionary2[temp].indexOf("Temp:")+5))]);
        });

        var dictionary3 = Object.values(data).at(0).SensorData3; 
        var keys3 = Object.keys(dictionary3);
        
        keys3.map((time) => {
          setTimesS3((oldArray) => 
          [...oldArray, dictionary3[time].substring(0,dictionary3[time].indexOf("Temp:"))]);
        });
        
        
        keys3.map((temp) => {
          setTempsS3((oldArray) => 
          [...oldArray, parseFloat(dictionary3[temp].substring(dictionary3[temp].indexOf("Temp:")+5))]);
        });

        var dictionary4 = Object.values(data).at(0).SensorData4; 
        var keys4 = Object.keys(dictionary4);
        
        keys4.map((time) => {
          setTimesS4((oldArray) => 
          [...oldArray, dictionary4[time].substring(0,dictionary4[time].indexOf("Temp:"))]);
        });
        
        
        keys4.map((temp) => {
          setTempsS4((oldArray) => 
          [...oldArray, parseFloat(dictionary4[temp].substring(dictionary4[temp].indexOf("Temp:")+5))]);
        });

        console.log(tempsS1.length)
      }
    });
  }, []);


  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />


      <div className="App">
        <Header/>
        <Container
        alignItems='center'
        sx={{
          mt: 5

        }}
        >
          
        <Typography
            component="h2"
            className="fade-list-item"
            style={{ animationDelay: `${0}ms`}}
            variant="h3"
            align="center"
            gutterBottom>
            History
          </Typography>

          <Stack
              sx={{ pt: 0 }}
              direction="row"
              spacing={2}
              justifyContent="center"
            >
              {/* <Button variant="contained">Contact</Button> */}
              <Button variant="contained" onClick={handleClick1}>Sensor 1</Button>
              <Button variant="contained" onClick={handleClick2}>Sensor 2</Button>
              <Button variant="contained" onClick={handleClick3}>Sensor 3</Button>
              <Button variant="contained" onClick={handleClick4}>Sensor 4</Button>
          </Stack>

          {isShown && tempsS1.filter(notEmptyTemp).reverse().map((temp, index) => (
            
            <>
              <Container
                className="fade-list-item"
                alignItems='center'
                sx={{
                  pt: 2,
                  pb: 2,
                  mt: 5

                }}
                justifyContent='center'
                style={{ animationDelay: `${150 * index}ms`, borderRadius: 20, color: 'white', backgroundColor: '#4287f5' }}
                component="main"
                maxWidth="xs">
                <Typography
                  component="h1"
                  variant="h6"
                  align="center"
                  gutterBottom>
                  {temp}&#176;F at {timesS1.reverse().at(index)}
                </Typography>

              </Container>
            </>
          ))}

          {isShown2 && tempsS2.filter(notEmptyTemp).reverse().map((temp, index) => (
            
            <>
              <Container
                className="fade-list-item"
                alignItems='center'
                sx={{
                  pt: 2,
                  pb: 2,
                  mt: 5

                }}
                justifyContent='center'
                style={{ animationDelay: `${150 * index}ms`, borderRadius: 20, color: 'white', backgroundColor: '#4287f5' }}
                component="main"
                maxWidth="xs">
                <Typography
                  component="h1"
                  variant="h6"
                  align="center"
                  gutterBottom>
                  {temp}&#176;F at {timesS2.reverse().at(index)}
                </Typography>

              </Container>
            </>
          ))} 

        {isShown3 && tempsS3.filter(notEmptyTemp).reverse().map((temp, index) => (
            
            <>
              <Container
                className="fade-list-item"
                alignItems='center'
                sx={{
                  pt: 2,
                  pb: 2,
                  mt: 5

                }}
                justifyContent='center'
                style={{ animationDelay: `${150 * index}ms`, borderRadius: 20, color: 'white', backgroundColor: '#4287f5' }}
                component="main"
                maxWidth="xs">
                <Typography
                  component="h1"
                  variant="h6"
                  align="center"
                  gutterBottom>
                  {temp}&#176;F at {timesS3.reverse().at(index)}
                </Typography>

              </Container>
            </>
          ))}   

        {isShown4 && tempsS4.filter(notEmptyTemp).reverse().map((temp, index) => (
            
            <>
              <Container
                className="fade-list-item"
                alignItems='center'
                sx={{
                  pt: 2,
                  pb: 2,
                  mt: 5

                }}
                justifyContent='center'
                style={{ animationDelay: `${150 * index}ms`, borderRadius: 20, color: 'white', backgroundColor: '#4287f5' }}
                component="main"
                maxWidth="xs">
                <Typography
                  component="h1"
                  variant="h6"
                  align="center"
                  gutterBottom>
                  {temp}&#176;F at {timesS4.reverse().at(index)}
                </Typography>

              </Container>
            </>
          ))}
          

        </Container>
      </div>
    </ThemeProvider>
  );
}

export default App;
